    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view v-if="loading" class="loading-overlay">
      <view class="loading-content">
        <text class="loading-icon">‚è≥</text>
        <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
      </view>
    </view>
  </view>
