<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

export default {
  components: { uniPopup },
  data() {
    return {
      gongdeItems: [],
      currentGongdeItem: {
        name: '',
        type: 'money',
        description: '',
        meritMultiplier: 2,
        status: 'ÂêØÁî®'
      },
      typeOptions: ['ÂñÑÊ¨æÊçêËµ†', 'ÂÆûÁâ©ÊçêËµ†'],
      typeIndex: 0,
      isEdit: false,
      editIndex: -1,
      loading: false,
      showGongdePopup: false
    }
  },
  computed: {
    enabledCount() {
      return this.gongdeItems.filter(item => item.status === 'ÂêØÁî®').length
    },
    disabledCount() {
      return this.gongdeItems.filter(item => item.status !== 'ÂêØÁî®').length
    }
  },
  onLoad() {
    this.loadGongdeItems()
  },
  methods: {
    async loadGongdeItems() {
      try {
        this.loading = true
        console.log('ÂºÄÂßãÂä†ËΩΩÂäüÂæ∑Â∏ÉÊñΩÊï∞ÊçÆ...')
        
        const result = await uniCloud.callFunction({ name: 'getGongdeItems' })
        console.log('ÂäüÂæ∑Â∏ÉÊñΩÊï∞ÊçÆÂä†ËΩΩÁªìÊûú:', result)
        
        if (result.result && result.result.data) {
          this.gongdeItems = result.result.data
          console.log('ÂäüÂæ∑Â∏ÉÊñΩÊï∞ÊçÆÂä†ËΩΩÊàêÂäüÔºåÂÖ±', this.gongdeItems.length, 'Êù°')
        } else {
          this.gongdeItems = []
          console.log('ÂäüÂæ∑Â∏ÉÊñΩÊï∞ÊçÆ‰∏∫Á©∫')
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂäüÂæ∑Â∏ÉÊñΩÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: 'Âä†ËΩΩÂ§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
          icon: 'none',
          duration: 3000
        })
        this.gongdeItems = []
      } finally {
        this.loading = false
      }
    },
    showAddModal() {
      console.log('ÊâìÂºÄÊ∑ªÂä†Â∏ÉÊñΩÈ°πÁõÆÂºπÁ™ó')
      this.isEdit = false
      this.editIndex = -1
      this.currentGongdeItem = { 
        name: '', 
        type: 'money', 
        description: '', 
        meritMultiplier: 2,
        status: 'ÂêØÁî®'
      }
      this.typeIndex = 0
      this.showGongdePopup = true
    },
    editGongdeItem(index) {
      this.isEdit = true
      this.editIndex = index
      this.currentGongdeItem = { ...this.gongdeItems[index] }
      this.typeIndex = this.currentGongdeItem.type === 'money' ? 0 : 1
      this.showGongdePopup = true
    },
    onTypeChange(e) {
      this.typeIndex = e.detail.value
      this.currentGongdeItem.type = this.typeIndex === 0 ? 'money' : 'goods'
    },
    async deleteGongdeItem(index) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§"${this.gongdeItems[index].name || 'Êú™ÂëΩÂêç'}"Â∏ÉÊñΩÈ°πÁõÆÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ`,
        confirmText: 'Âà†Èô§',
        confirmColor: '#ff3b30',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Âà†Èô§‰∏≠...' })
              await uniCloud.callFunction({
                name: 'deleteGongdeItem',
                data: { id: this.gongdeItems[index]._id }
              })
              this.gongdeItems.splice(index, 1)
              uni.hideLoading()
              uni.showToast({ 
                title: 'Âà†Èô§ÊàêÂäü', 
                icon: 'success',
                duration: 2000
              })
            } catch (error) {
              uni.hideLoading()
              uni.showToast({ 
                title: 'Âà†Èô§Â§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
                icon: 'none',
                duration: 3000
              })
              console.error('Âà†Èô§Â∏ÉÊñΩÈ°πÁõÆÂ§±Ë¥•:', error)
            }
          }
        }
      })
    },
    async saveGongdeItem() {
      if (!this.currentGongdeItem.name) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•È°πÁõÆÂêçÁß∞', icon: 'none' })
        return
      }
      
      try {
        this.loading = true
        console.log('‰øùÂ≠òÂäüÂæ∑Â∏ÉÊñΩÊï∞ÊçÆ:', this.currentGongdeItem)
        
        if (this.isEdit) {
          console.log('Êõ¥Êñ∞Â∏ÉÊñΩÈ°πÁõÆÔºåID:', this.gongdeItems[this.editIndex]._id)
          const updateResult = await uniCloud.callFunction({
            name: 'updateGongdeItem',
            data: { id: this.gongdeItems[this.editIndex]._id, item: this.currentGongdeItem }
          })
          console.log('Êõ¥Êñ∞ÁªìÊûú:', updateResult)
          this.gongdeItems[this.editIndex] = { ...this.currentGongdeItem }
        } else {
          console.log('Ê∑ªÂä†Êñ∞Â∏ÉÊñΩÈ°πÁõÆ')
          const result = await uniCloud.callFunction({
            name: 'addGongdeItem',
            data: { item: this.currentGongdeItem }
          })
          console.log('Ê∑ªÂä†ÁªìÊûú:', result)
          if (result.result && result.result.data) {
            this.gongdeItems.push(result.result.data)
          } else {
            throw new Error('Ê∑ªÂä†Â§±Ë¥•ÔºöËøîÂõûÊï∞ÊçÆÊ†ºÂºèÈîôËØØ')
          }
        }
        this.closePopup()
        uni.showToast({ title: this.isEdit ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü', icon: 'success' })
      } catch (error) {
        console.error('‰øùÂ≠òÂ∏ÉÊñΩÈ°πÁõÆÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: '‰øùÂ≠òÂ§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
          icon: 'none',
          duration: 3000
        })
      } finally {
        this.loading = false
      }
    },
    closePopup() {
      console.log('ÂÖ≥Èó≠ÂºπÁ™ó')
      this.showGongdePopup = false
      this.currentGongdeItem = { 
        name: '', 
        type: 'money', 
        description: '', 
        meritMultiplier: 2,
        status: 'ÂêØÁî®'
      }
      this.typeIndex = 0
    },
    async toggleGongdeItemStatus(item, e) {
      const newStatus = e.detail.value ? 'ÂêØÁî®' : 'Á¶ÅÁî®'
      const oldStatus = item.status
      
      try {
        console.log('ÂàáÊç¢Â∏ÉÊñΩÈ°πÁõÆÁä∂ÊÄÅ:', item._id, oldStatus, '->', newStatus)
        
        if (!item._id) {
          throw new Error('Â∏ÉÊñΩÈ°πÁõÆID‰∏çÂ≠òÂú®')
        }
        
        item.status = newStatus
        
        const result = await uniCloud.callFunction({ 
          name: 'updateGongdeItem', 
          data: { 
            id: item._id, 
            item: { status: newStatus } 
          } 
        })
        
        console.log('Áä∂ÊÄÅÊõ¥Êñ∞ÁªìÊûú:', result)
        
        uni.showToast({ 
          title: `${newStatus}ÊàêÂäü`, 
          icon: 'success',
          duration: 1500
        })
      } catch (error) {
        item.status = oldStatus
        console.error('Áä∂ÊÄÅÊõ¥Êñ∞Â§±Ë¥•:', error)
        uni.showToast({ 
          title: 'Áä∂ÊÄÅÊõ¥Êñ∞Â§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
          icon: 'none',
          duration: 3000
        })
      }
    }
  }
}
</script>

